body {
    /* flex: 1;
    flex-direction: column;
    justify-content: center; */
    padding: 0;
    font: 14px "Century Gothic", Futura, sans-serif;
    font-family: "Clear Sans", "Helvetica Neue", Arial, sans-serif;
    margin: 20px;
    background: #faf8ef;
    color: #776e65
  }
  
  ol, ul {
    padding-left: 30px;
  }

  .gameContainer {
    position: absolute;
    width: 455px;
    height: 625px;
    top: 50%;
    left: 50%;
    margin-left: -223px;
    margin-top: -343px;
  }

  #header {
    text-align: center;
    width: 435px;
    border: 10px solid #faf8ef;
    height: 70px;
    background: #faf8ef;
    font-size: 55px;
    font-weight: bold;
    color: #776e65;
    border-bottom: 5px;
  }

  #board {
    width: 455px;
    height: 585px;
    background: #bbada0;
    border-radius: 20px;
  }
  
  .board-row {
    clear: both;
    content: "";
    display: table;
  }

  .square {
    background: rgba(238, 228, 218, 0.35);
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 100px;
    margin-left: 11px;
    margin-top: 11px;
    padding: 0;
    text-align: center;
    width: 100px;
    border-radius: 5px;
  }

  .active-square {
    flex-direction: column;
    background: rgb(226, 160, 83);
    float: left;
    font-size: 50px;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    line-height: 34px;
    height: 100px;
    margin-left: 11px;
    margin-top: 11px;
    /* padding: 0; */
    text-align: center;
    width: 100px;
    border-radius: 5px;
  }

  .score-board {
    font-size: 50px;
    background: rgb(238, 217, 160);
    width: 435px;
    height: 70px;
    border-radius: 20px;
    text-align: center;
    border: 10px solid #bbada0;;
  }

  .restart-button {
    font-size: 20px;
    font-weight: bold;
    color: aliceblue;
    text-align: center;
    background: rgb(179, 163, 120);
    height: 40px;
    width: 300px;
    border-radius: 10px;
    margin-left: 80px;
    margin-top:10px;
  }